/* Overlay */
.home::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(8, 8, 8, 0.779); 
  z-index: -1; 
}
.home-content h3 {
  font-family: 'GFS Didot', serif;
