}
.eme{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(193, 117, 67);
  z-index: -1; 

}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.home img {
  height: 450px;
  width: auto;
  margin-top: 5.5rem;
  margin-left: 8rem;
  margin-right: 3rem; 

  /* ✨ Animation */
  opacity: 0; /* Start hidden */
  animation: fadeInUp 0.8s ease-out forwards;
}

